import React from 'react';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Container,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

const CreateNewAccount = () => {
  const classes = useStyles();
  const navigate = useNavigate();

  return (
    <Page
      className={classes.root}
      title="Create shop"
    >
      <Box
        display="flex"
        flexDirection="column"
        // height="100%"
        justifyContent="center"
      >
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              email: '',
              firstName: '',
              lastName: '',
              password: '',
              policy: false
            }}
            validationSchema={
              Yup.object().shape({
                name: Yup.string().max(255).required('Shop name is required'),
                detail: Yup.string().max(255).required('Shop Details is required'),
                email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
                address: Yup.string().max(255).required('Shop Address is required'),
                phone: Yup.string().max(255).required('Shop Phone is required'),
                city: Yup.string().max(255).required('City is required'),
                stateOrProvince: Yup.string().max(255).required('State Or Province is required'),
                shipmentMethod: Yup.string().max(255).required('Shipment methods is required'),
                paymentMethod: Yup.string().max(255).required('Payment methods is required'),
                // password: Yup.string().max(255).required('password is required'),
                // policy: Yup.boolean().oneOf([true], 'This field must be checked')
              })
            }
            onSubmit={() => {
              navigate('/app/dashboard', { replace: true });
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography
                    color="textPrimary"
                    variant="h2"
                  >
                    Create new Shop
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Use your shop to sell products on Car-Parts Pro
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.name && errors.name)}
                  fullWidth
                  helperText={touched.name && errors.name}
                  label="Shop name"
                  margin="normal"
                  name="shopNname"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.name}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.detail && errors.detail)}
                  fullWidth
                  helperText={touched.detail && errors.detail}
                  label="Shop Details"
                  margin="normal"
                  name="shopDetails"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.detail}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.email && errors.email)}
                  fullWidth
                  helperText={touched.email && errors.email}
                  label="Shop Email Address"
                  margin="normal"
                  name="shopEmail"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="email"
                  value={values.email}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.address && errors.address)}
                  fullWidth
                  helperText={touched.address && errors.address}
                  label="Shop Address"
                  margin="normal"
                  name="shopAddress"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.address}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.phone && errors.phone)}
                  fullWidth
                  helperText={touched.phone && errors.phone}
                  label="Shop Phone"
                  margin="normal"
                  name="shopPhone"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.phone}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.city && errors.city)}
                  fullWidth
                  helperText={touched.city && errors.city}
                  label="Shop City"
                  margin="normal"
                  name="shopCity"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.city}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.stateOrProvince && errors.stateOrProvince)}
                  fullWidth
                  helperText={touched.stateOrProvince && errors.stateOrProvince}
                  label="State / Provine"
                  margin="normal"
                  name="shopProvince"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.stateOrProvince}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.shipmentMethod && errors.shipmentMethod)}
                  fullWidth
                  helperText={touched.shipmentMethod && errors.shipmentMethod}
                  label="Shipment Methods"
                  margin="normal"
                  name="shipment"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.shipmentMethod}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.paymentMethod && errors.paymentMethod)}
                  fullWidth
                  helperText={touched.paymentMethod && errors.paymentMethod}
                  label="Payment Methods"
                  margin="normal"
                  name="paymentMethods"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.paymentMethod}
                  variant="outlined"
                />

                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Create
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Page>
  );
};

export default CreateNewAccount;
